		onPullDownRefresh:function(){
			console.log('下拉刷新')
			
		},
		onShow:function(){

































			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyNoRead",
				data: {
					auid:this.userid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					this.iosapy=res.data.data.pay
					if(res.data.code=="1"){
						this.msgNumber=res.data.data.count
					}else{
			
					}
