			},
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
				this.AvatarUrl=server.userinfo.AvatarUrl;
				this.username=server.userinfo.UserName;
				this.zhongcao=server.userinfo.ZhongcaoCount;
				this.guanzhu=server.userinfo.FollowedCount;
				this.fensi=server.userinfo.FollowerCount;
				this.dengji=server.userinfo.Rank;
				this.userid=server.userinfo.Auid;
				console.log("测试！：",this.username)
				//请求 https://api.angeli.top/user.php?type=getMyNoRead
				uni.request({
					method:'GET',
					url: "https://api.angeli.top/user.php?type=getMyNoRead", 
					data: {
						auid:this.userid,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					success: (res) => {
						console.log(res)
						if(res.data.code=="1"){
							this.msgNumber=res.data.data.count
						}else{
							uni.navigateTo({
								url: '../reg/reg'
							})
							this.AvatarUrl='https://c-ssl.duitang.com/uploads/item/201807/01/20180701122340_uxlwc.thumb.700_0.jpeg';
							this.username='未登录';
							this.zhongcao=0;
							this.guanzhu=0;
							this.fensi=0;
							this.dengji=0;
							this.userid=0;
							this.userInfo=null;
						}
						console.log(this.msgNumber)
					},
					complete() {
						
					}
				});
			},
			hideModal(e) {
				this.modalName = null
				this.showYs=false
				uni.setStorageSync('showYs', '1');
			},
			tabSelect(e) {
				this.TabCur = e
				this.scrollTop = this.old.scrollTop
				this.$nextTick(function() {
					this.scrollTop = 0
				});
				console.log(e)
				if(e==0){ //最新
					this.getPostData('new',0);
					this.postype='new'
				}else if(e==1){ //最热
					this.postype='hot'
					this.getPostData('hot',0);
					
				}else if(e==2){ //关注
					this.postype='guanzhu'
					this.getPostData('guanzhu',0);
				}
				
			},
			Like:function(postid,auid,give,index,zc){
				if(server.token==""||server.token==null){
					uni.showToast({
						title: "你还没有登录，请登录后再来吧",
						position:'bottom',
						icon:'none'
					});
					setTimeout(function () {
						uni.navigateTo({
							url: '../reg/reg'
						})
					}, 1200);
					return;
				}
				
				if(auid==server.userinfo.Auid){
					uni.showToast({
						title: "不能给自己种草",
						position:'bottom',
						icon:'none'
					});
					return;
				}
				
				if(give===true){
					var modea='del'
				}else{
					var modea='add'
				}
				uni.request({
					method:'GET',
					url: "https://api.angeli.top/post.php?type=Like", 
					data: {
						fuid: auid,
						postid:postid,
						mode:modea,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'system':server.system
					},
					success: (res) => {
						if(res.data.code=="1"){
							if(modea=='add'){
								this.postList[index].ZhongcaoCount=Number(zc)+1;
								this.postList[index].Give=true;
								uni.showToast({
									title: "种草成功！",
									position:'bottom',
									icon:'none'
								});
							}else{
								this.postList[index].ZhongcaoCount=Number(zc)-1;
								this.postList[index].Give=false;
								uni.showToast({
									title: "取消种草成功！",
									position:'bottom',
									icon:'none'
								});
							}
							this.$forceUpdate()
						}else{
							
							uni.showToast({
								title: "种草失败！",
								position:'bottom',
								icon:'none'
							});
						}
					},
					complete() {
						
					}
				});
			},
			caidan:function(resa){
				console.log(resa)
				this.Dindex=resa;
				if(resa.AuthorId==server.userinfo.Auid){
					this.menuList=['分享', '举报','删除帖子']
				}else{
					this.menuList=['分享', '举报']
				}
				uni.showActionSheet({
					itemList:this.menuList,
					success: (res) =>{
						switch(res.tapIndex){
							case 0:
								this.showAppFenxiang=true
								break;
							case 1:
								this.showJubao=true;
								this.jubao.postid=resa.PostsId;
								this.jubao.authorid=resa.AuthorId
								
								
								break;
							case 2:
								this.$delPost(resa.PostsId);
								break;
							default:
								
						}
					},
					fail: function (res) {
						console.log(res.errMsg);
					}
				});
			},
			getHei(){
				
				let view = uni.createSelectorQuery().select("#topbox");
				view.fields({
						  size: true,
						  scrollOffset: true
						}, data => {
						  this.gaodu=data.height-5;
						  console.log("总高度：" + this.gaodu);
						}).exec();
			},
			showImage(res,c){
				var imageurl=res[c];
				uni.previewImage({
					current:c,
					urls: res,
					longPressActions: {
					itemList: ['保存图片'],
						success: (resa) => {
							
							uni.downloadFile({
							    url: imageurl, 
							    success: (res) => {
									console.log(res)
							        if (res.statusCode === 200) {
							           uni.saveImageToPhotosAlbum({
										   filePath: res.tempFilePath,
										   success: function () {
											  uni.showToast({
											  	title: '已保存',
											  	position:'bottom',
											  	icon:'none'
											  });
										   }
									   });
							        }else{
										uni.showToast({
											title: '保存失败',
											position:'bottom',
											icon:'none'
										});
									}
							    }
							});
							
						},
						fail: function (res) {
							console.log(res.errMsg);
						}    
					}
				})
			},
			getPostData(type,classId){
				console.log(type)
				this.page=1
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/post.php?type=outPostList',
					data: {
						page: 1,
						postType:type,
						count:10,
						classId:classId,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					success: (res) => {
						
						this.postList=[]
						console.log(res.data.data);
						console.log("————————————帖子列表——————————");
						this.postList=res.data.data;
						console.log(this.postList);
						this.weikong=false
						if(res.data.code!=="1"){
							uni.showToast({
								title: '获取帖子失败，建议重启',
								position:'bottom',
								icon:'none'
							});
						}
						if(res.data.data==false){
							this.weikong=true
						}
						//this.$forceUpdate();
					},
					complete: () => {
						this.$forceUpdate();
					}
				});
				
			},
